.login__container {
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: #dcdcdc;
  padding: 30px;
  width: 280px;
  margin-left: 35px;
}
.login__textBox {
  padding: 10px;
  font-size: 18px;
  margin-bottom: 10px;
}
.login__btn {
  padding: 10px;
  font-size: 18px;
  margin-bottom: 10px;
  border: none;
  color: white;
  background-color: black;
}

.login__btn:hover {
  cursor: pointer;
}
.login__google {
  background-color: #4285f4;
}
.login div {
  margin-top: 7px;
}

.logout:hover {
  cursor: pointer;
}

@media only screen and (min-width: 768px) {
  .spanname {
    letter-spacing: 1px;
    position: relative;
    top: 0;
    font-size: 10px;
    margin-right: 10px;
    margin-top: 25px;
  }
}

@media only screen and (max-width: 479px) {
  .spanname {
    letter-spacing: 1px;
    position: relative;
    top: 0;
    font-size: 6px;
    margin-top: 32px;
  }
}
