html {
  scroll-behavior: smooth;
  overflow-x: hidden !important ;
  height: 100%;
}

body {
  overflow-x: hidden !important ;
}

.bl:hover {
  color: lightblue;
}

.logbtn:hover {
  color: black !important ;
  background-color: white !important;
  transition-timing-function: ease-in;
  transition: all 0.5s;
  border-color: transparent;
}

body::-webkit-scrollbar {
